.skill-panel p {
  font-size: 0.95rem;
  letter-spacing: 0.02em;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: clamp(20px, 3.5vw, 34px);
}

.project-panel {
  display: grid;
  gap: 14px;
  border: var(--border-thick);
  border-radius: 18px;
  background: #fff;
  box-shadow: 5px 5px 0 var(--ink);
  padding: clamp(20px, 3vw, 28px);
  position: relative;
  overflow: hidden;
}

.project-panel::before {
  content: '';
  position: absolute;
  inset: 12px;
  border: 3px solid rgba(0, 0, 0, 0.08);
  border-radius: 14px;
  pointer-events: none;
}

.project-panel__meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  font-family: var(--text-display);
  letter-spacing: 0.08em;
  text-transform: uppercase;
  font-size: 0.85rem;
}

.project-panel__code {
  background: rgba(0, 0, 0, 0.1);
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 999px;
  padding: 4px 12px;
}

.project-panel__date {
  letter-spacing: 0.12em;
}

.project-panel h3 {
  font-family: var(--text-display);
  font-size: 1.35rem;
  letter-spacing: 0.06em;
}

.project-panel__stack {
  font-weight: 600;
  letter-spacing: 0.04em;
  font-size: 0.9rem;
}

.project-panel__list {
  list-style: square;
  margin-left: 20px;
  display: grid;
  gap: 10px;
  font-size: 0.94rem;
  letter-spacing: 0.01em;
}

.project-panel__link {
  justify-self: flex-start;
}

.experience-grid {
  display: grid;
  gap: clamp(20px, 3vw, 30px);
}

.experience-panel {
  border: var(--border-thick);
  border-radius: 18px;
  background: #fff;
  box-shadow: 5px 5px 0 var(--ink);
  padding: clamp(20px, 3vw, 28px);
  display: grid;
  gap: 16px;
  position: relative;
}

.experience-panel__stamp {
  position: absolute;
  top: -14px;
  left: clamp(18px, 5vw, 40px);
  background: var(--accent-blue);
  color: var(--paper);
  padding: 6px 16px;
  border: var(--border-thick);
  border-radius: 999px;
  font-family: var(--text-display);
  letter-spacing: 0.12em;
  transform: rotate(-4deg);
}

.experience-panel header {
  display: grid;
  gap: 8px;
}

.experience-panel h3 {
  font-family: var(--text-display);
  font-size: 1.3rem;
  letter-spacing: 0.06em;
}

.experience-panel__meta {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  font-weight: 600;
  letter-spacing: 0.04em;
}

.experience-panel__org {
  text-transform: uppercase;
}

.experience-panel__period {
  font-size: 0.85rem;
  letter-spacing: 0.08em;
}

.experience-panel ul {
  list-style: square;
  margin-left: 20px;
  display: grid;
  gap: 10px;
  font-size: 0.95rem;
}

.education-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: clamp(20px, 3vw, 34px);
}

.education-panel,
.credential-panel,
.volunteer-panel {
  border: var(--border-thick);
  border-radius: 18px;
  background: #fff;
  box-shadow: 5px 5px 0 var(--ink);
  padding: clamp(20px, 3vw, 28px);
  display: grid;
  gap: 14px;
  position: relative;
}

.education-panel header {
  display: grid;
  gap: 6px;
}

.education-panel h3,
.credential-panel h3,
.volunteer-panel h3 {
  font-family: var(--text-display);
  font-size: 1.3rem;
  letter-spacing: 0.08em;
}

.education-panel__school,
.education-panel__period {
  font-weight: 600;
  letter-spacing: 0.06em;
}

.education-panel__focus {
  font-size: 0.95rem;
  letter-spacing: 0.02em;
}

.education-panel__tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.education-panel__tags li {
  list-style: none;
  font-family: var(--text-display);
  letter-spacing: 0.08em;
  font-size: 0.75rem;
  text-transform: uppercase;
  padding: 6px 10px;
  border: 2px solid rgba(0, 0, 0, 0.18);
  border-radius: 999px;
  background: rgba(0, 87, 255, 0.12);
}

.credential-panel ul {
  list-style: none;
  display: grid;
  gap: 10px;
}

.credential-panel li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  font-size: 0.95rem;
  letter-spacing: 0.02em;
}

.credential-panel time {
  font-family: var(--text-display);
  letter-spacing: 0.1em;
  font-size: 0.75rem;
}

.volunteer-panel__title {
  font-weight: 700;
  letter-spacing: 0.04em;
}

.volunteer-panel__org,
.volunteer-panel__period {
  font-family: var(--text-display);
  letter-spacing: 0.08em;
  text-transform: uppercase;
  font-size: 0.85rem;
}

.kochi-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  border: var(--border-thick);
  border-radius: 999px;
  background: var(--accent-blue);
  color: var(--paper);
  font-family: var(--text-display);
  letter-spacing: 0.08em;
  text-transform: uppercase;
  font-size: 0.9rem;
  cursor: pointer;
  transition: transform var(--transition), box-shadow var(--transition);
  text-decoration: none;
}

.kochi-button:hover,
.kochi-button:focus {
  transform: translate(-2px, -2px);
  box-shadow: var(--shadow-tight);
}

.kochi-button--secondary {
  background: var(--accent-gold);
  color: var(--ink);
}

.kochi-button--outline {
  background: transparent;
  color: var(--ink);
  border-color: var(--ink);
}

.hero-main__title {
  font-family: var(--text-display);
  font-size: clamp(2.8rem, 6vw, 3.6rem);
  letter-spacing: 0.08em;
  text-transform: uppercase;
  line-height: 1.1;
  margin-bottom: 8px;
}

.hero-main__subtitle {
  font-size: 1.1rem;
  letter-spacing: 0.04em;
  margin-bottom: 16px;
  color: rgba(0, 0, 0, 0.8);
}

.hero-main__overline {
  font-family: var(--text-display);
  font-size: 0.9rem;
  letter-spacing: 0.16em;
  text-transform: uppercase;
  color: var(--accent-blue);
  margin-bottom: 4px;
}

.hero-main__contacts {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  font-size: 0.95rem;
  letter-spacing: 0.02em;
  margin-bottom: 20px;
}

.hero-main__contacts span {
  display: flex;
  align-items: center;
  gap: 8px;
}

.hero-main__highlights {
  list-style: none;
  display: grid;
  gap: 12px;
  margin-bottom: 24px;
}

.hero-main__highlights li {
  padding-left: 24px;
  position: relative;
}

.hero-main__highlights li::before {
  content: '';
  position: absolute;
  left: 0;
  color: var(--accent-red);
  font-weight: bold;
}

.hero-main__actions {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.kochi-footer {
  background: linear-gradient(135deg, #fffdfa 0%, rgba(0, 87, 255, 0.12) 100%);
  border: var(--border-thick);
  border-radius: 20px;
  box-shadow: 6px 6px 0 var(--ink);
  margin: clamp(40px, 6vw, 60px) auto 24px;
  max-width: 1080px;
  padding: clamp(24px, 4vw, 36px);
}

.kochi-footer__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 18px 24px;
}

.kochi-footer__title {
  font-family: var(--text-display);
  font-size: 1.6rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.kochi-footer__subtitle {
  display: block;
  font-weight: 600;
  letter-spacing: 0.08em;
}

.kochi-footer ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 12px 18px;
  font-weight: 600;
  letter-spacing: 0.04em;
}

.kochi-footer a {
  text-decoration: underline;
}

.kochi-footer__copyright {
  font-family: var(--text-display);
  letter-spacing: 0.12em;
  text-transform: uppercase;
  font-size: 0.75rem;
}

.footer-note {
  text-align: center;
  font-family: var(--text-display);
  letter-spacing: 0.08em;
  font-size: 1rem;
  text-transform: uppercase;
  margin: 36px auto 12px;
  color: rgba(0, 0, 0, 0.6);
}

@media (max-width: 980px) {
  .kochi-nav ul {
    display: none;
  }

  .kochi-toggle {
    display: inline-flex;
  }

  .hero-grid {
    grid-template-columns: 1fr;
  }

  .panel__badge {
    left: 24px;
  }

  .hero-main__summary {
    max-width: unset;
  }
}

@media (max-width: 768px) {
  body {
    padding-bottom: 32px;
  }

  .kochi-banner__inner {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }

  .kochi-brand {
    width: 100%;
    justify-content: space-between;
  }

  .panel {
    margin: clamp(22px, 5vw, 38px) 14px;
    padding: clamp(20px, 5vw, 34px);
  }

  .hero-main__contacts {
    flex-direction: column;
    align-items: flex-start;
  }

  .panel--hero::before {
    inset: -40px -16px;
  }

  .panel__badge {
    top: -14px;
  }

  .project-grid,
  .education-grid,
  .experience-grid {
    grid-template-columns: 1fr;
  }

  .closing-card__actions {
    flex-direction: column;
  }

  .kochi-footer__inner {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (max-width: 600px) {
  .hero-main__title {
    font-size: clamp(2.2rem, 8vw, 3rem);
  }

  .hero-main__highlights li {
    padding-left: 38px;
  }

  .badge-emblem {
    position: relative;
    inset: unset;
    justify-self: center;
    transform: rotate(-4deg);
  }

  .badge-card__title {
    max-width: 100%;
    text-align: center;
  }

  .badge-card__header {
    justify-items: center;
  }

  .badge-card__meta {
    justify-content: center;
  }

  .badge-card__footer {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .panel {
    margin: clamp(18px, 6vw, 28px) 12px;
    padding: clamp(18px, 6vw, 28px);
  }

  .hero-main__overline {
    font-size: 0.68rem;
  }

  .hero-main__subtitle {
    font-size: 0.85rem;
  }

  .hero-main__contacts {
    font-size: 0.88rem;
  }

  .hero-main__actions {
    width: 100%;
    flex-direction: column;
  }

  .kochi-button {
    justify-content: center;
    width: 100%;
  }

  .badge-card__meta {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (max-width: 360px) {
  .hero-main__title {
    font-size: 2rem;
  }

  .panel {
    padding: 18px;
  }

  .hero-sidebar__availability {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .hero-sidebar__availability-icon {
    justify-self: center;
  }

  .hero-sidebar__availability-text {
    justify-items: center;
  }
}

@media (max-width: 320px) {
  .panel {
    padding: 16px;
  }

  .panel__badge {
    top: -18px;
  }

  .badge-card__meta {
    gap: 8px;
  }
}


.testimonial-panel {
  border: var(--border-thick);
  border-radius: 18px;
  background: #fff;
  box-shadow: 5px 5px 0 var(--ink);
  padding: clamp(20px, 3vw, 28px);
  display: grid;
  gap: 14px;
  position: relative;
}

.testimonial-panel blockquote {
  margin: 0;
  font-style: italic;
  font-size: 1.1rem;
  line-height: 1.4;
  position: relative;
  background: #fff;
  border: var(--border-thick);
  border-radius: 18px;
  padding: 20px;
  box-shadow: var(--shadow);
}

.testimonial-panel blockquote::before {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 20px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid var(--ink);
}

.testimonial-panel blockquote::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 22px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #fff;
}

.testimonial-panel cite {
  font-weight: 600;
  font-style: normal;
  color: var(--accent-blue);
}

.hero-sidebar__badge {
  background: var(--accent-blue);
  color: var(--paper);
  padding: 8px 12px;
  border: var(--border-thick);
  border-radius: 999px;
  font-family: var(--text-display);
  letter-spacing: 0.08em;
  font-size: 0.8rem;
  text-align: center;
  transform: rotate(-2deg);
  box-shadow: var(--shadow-tight);
}

.hero-sidebar__badge span {
  display: block;
  font-size: 0.9rem;
  margin-bottom: 2px;
}

.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid var(--halftone);
  border-top: 4px solid var(--accent-blue);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Skip link for accessibility */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: var(--accent-blue);
  color: var(--paper);
  padding: 8px 16px;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 600;
  z-index: 1000;
  transition: top 0.3s;
}

.skip-link:focus {
  top: 6px;
}
